import { useMutation, useQuery, useQueryClient, type UseMutationOptions, type UseQueryOptions } from '@tanstack/react-query';
import { apiService, type RoleRequest } from '../services/api';

export function useRoleRequests(
  status?: string,
  options?: Omit<UseQueryOptions<RoleRequest[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['role-requests', status ?? 'all'] as const;
  return useQuery<RoleRequest[], Error>({
    queryKey: key,
    queryFn: () => apiService.getRoleRequests(status),
    ...(options ?? {}),
  });
}

export function useApproveRoleRequest(
  options?: UseMutationOptions<{ message: string; user_id: string; new_role: string }, Error, { requestId: string }>
) {
  const qc = useQueryClient();
  return useMutation({
    mutationFn: ({ requestId }) => apiService.approveRoleRequest(requestId),
    onSuccess: (...args) => {
      qc.invalidateQueries({ queryKey: ['role-requests'] });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}

export function useRejectRoleRequest(
  options?: UseMutationOptions<{ message: string; request_id: string }, Error, { requestId: string; reason?: string }>
) {
  const qc = useQueryClient();
  return useMutation({
    mutationFn: ({ requestId, reason }) => apiService.rejectRoleRequest(requestId, reason),
    onSuccess: (...args) => {
      qc.invalidateQueries({ queryKey: ['role-requests'] });
      options?.onSuccess?.(...args);
    },
    ...options,
  });
}
